@import "components/mixins", "components/vars";

body {
  margin: 0 auto;
  padding: 1rem 3rem 1rem 3rem;
  
}

.fourPlayers{
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 0.5rem;
  .playerOne{
    background-color: $primary-color;
  }
  .playerTwo{
    background-color: $secondary-color;
  }
  .playerThree{
    background-color: $terciary-color;
  }
  .playerFour{
    background-color: $four-color;
  }
}

.medallas {
  padding: 1rem 0;
  input{
    display: none; 
  }
  label {
    display: contents;
    img{
      margin-left: 0.1rem;
      max-width: 12%;
    }
  }
}

.toDoList {
  background-color: #212529;
  padding: 1rem 0;
  label {
    margin-left: 1rem;
  }
  .toDoNew {
    margin-left: 1rem;
  }
}

.contadores{
  display: flex;
  gap: 3rem;
  .tickets{
    display: flex;
    align-items: baseline;
    gap: 0.5rem;
    padding: 1rem 0 0 0;
  }
}

.apiContainer{
  display: flex;
  justify-content: space-around;
  margin: 2rem 0 1rem 0;
  img{
     width: 15rem;   
  }
}

.card{
    text-transform:capitalize;
}