body {
  margin: 0 auto;
  padding: 1rem 3rem 1rem 3rem;
}

.fourPlayers {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 0.5rem;
}
.fourPlayers .playerOne {
  background-color: #e68494;
}
.fourPlayers .playerTwo {
  background-color: #5e7ce0;
}
.fourPlayers .playerThree {
  background-color: #92df92;
}
.fourPlayers .playerFour {
  background-color: #dfdd82;
}

.medallas {
  padding: 1rem 0;
}
.medallas input {
  display: none;
}
.medallas label {
  display: contents;
}
.medallas label img {
  margin-left: 0.1rem;
  max-width: 12%;
}

.toDoList {
  background-color: #212529;
  padding: 1rem 0;
}
.toDoList label {
  margin-left: 1rem;
}
.toDoList .toDoNew {
  margin-left: 1rem;
}

.contadores {
  display: flex;
  gap: 3rem;
}
.contadores .tickets {
  display: flex;
  align-items: baseline;
  gap: 0.5rem;
  padding: 1rem 0 0 0;
}

.apiContainer {
  display: flex;
  justify-content: space-around;
  margin: 2rem 0 1rem 0;
}
.apiContainer img {
  width: 15rem;
}

.card {
  text-transform: capitalize;
}

